*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  margin: 0;
}

.h1 {
  margin-top: 0;
  font-weight: 700;
  font-size: 60px;
  line-height: 70px;
  margin-bottom: 15px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
}
.h1 b {
  margin-bottom: 10px;
  font-weight: 800;
  display: block;
}
@media (max-width: 1250px) {
  .h1 {
    width: 540px;
    margin: 0 auto;
    max-width: 100%;
    font-size: 40px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .h1 {
    font-size: 30px;
    line-height: 47px;
  }
}

.h2 {
  position: relative;
  font-weight: 700;
  font-size: 28px;
  line-height: 38px;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  display: flex;
  align-items: center;
}
.h2::before, .h2::after {
  content: "";
  flex-grow: 1;
  background: #dfe4e0;
  height: 1px;
  z-index: -1;
}
.h2::before {
  margin-right: 35px;
}
.h2::after {
  margin-left: 35px;
}
@media (max-width: 767px) {
  .h2 {
    font-size: 24px;
    line-height: 33px;
  }
}

.h3 {
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
  margin-top: 12px;
  margin-bottom: 22px;
}

.section {
  padding-top: 100px;
}
@media (max-width: 1250px) {
  .section {
    padding-top: 80px;
  }
}
@media (max-width: 1250px) {
  .section {
    padding-top: 40px;
  }
}
.section_gray, .section_orange {
  margin-top: 100px;
  background: rgba(217, 217, 217, 0.2);
  padding-bottom: 100px;
}
@media (max-width: 1250px) {
  .section_gray, .section_orange {
    margin-top: 80px;
    padding-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .section_gray, .section_orange {
    margin-top: 40px;
    padding-bottom: 40px;
  }
}
.section_orange {
  margin-top: 0;
  background: rgba(244, 151, 37, 0.1);
}
.section.tech {
  padding-bottom: 100px;
}
@media (max-width: 767px) {
  .section.tech {
    padding-bottom: 40px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.logo {
  display: flex;
}
@media (max-width: 767px) {
  .logo {
    max-height: 34px;
  }
}

.promo {
  background: #000 url(../../img/bg.jpg) no-repeat;
  background-size: cover;
}
@media (max-width: 767px) {
  .promo {
    background-position: bottom;
  }
}

.container {
  margin: 0 auto;
  width: 1250px;
  max-width: 100%;
  padding: 0 15px;
}
@media (max-width: 1250px) {
  .container {
    width: 710px;
  }
}

.logo {
  flex-shrink: 0;
}

.header__container {
  width: 1750px;
  display: flex;
  align-items: center;
  min-height: 80px;
}
@media (max-width: 1250px) {
  .header__container {
    width: 710px;
  }
}
@media (max-width: 767px) {
  .header__container {
    min-height: 60px;
    padding: 0;
  }
}

.header__schredule {
  font-weight: 700;
  margin-left: 25px;
}
@media (max-width: 767px) {
  .header__schredule {
    font-size: 12px;
    line-height: 15px;
    margin-left: 10px;
  }
}

.header__anchor {
  margin-left: auto;
  display: flex;
  align-items: center;
  margin-right: 35px;
  text-decoration-line: underline;
  text-transform: uppercase;
  color: #dc7900;
  font-weight: 700;
  cursor: pointer;
}
.header__anchor img {
  margin-right: 7px;
}
@media (max-width: 1250px) {
  .header__anchor {
    display: none;
  }
}

.phone {
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 22px;
  display: flex;
  align-items: center;
  text-transform: uppercase;
  color: #40404c;
  font-weight: 700;
}
.phone img {
  margin-right: 7px;
}
@media (max-width: 1250px) {
  .phone {
    margin-left: auto;
  }
}
@media (max-width: 767px) {
  .phone {
    margin-left: 0;
  }
}

@media (max-width: 767px) {
  .header .phone {
    display: none;
  }
}

.promo__content {
  width: 920px;
  max-width: 100%;
  margin: 0 auto;
  padding-top: 90px;
  padding-bottom: 100px;
  text-align: center;
}
@media (max-width: 1250px) {
  .promo__content {
    padding: 40px 0 60px;
  }
}

.promo__subtitle {
  font-weight: 700;
  font-size: 32px;
  line-height: 40px;
  color: #ffffff;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .promo__subtitle {
    font-size: 24px;
    line-height: 35px;
  }
}

.promo__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 262px;
  min-height: 60px;
  border-radius: 31px;
  font-weight: 600;
  font-size: 16px;
  color: #ffffff;
  border: none;
  background: #6c6c6c;
  transition: all 0.3s;
  cursor: pointer;
}
.promo__btn:hover {
  background: #f49725;
}

.promo__btns {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 570px;
  max-width: 100%;
  flex-wrap: wrap;
  margin: 45px auto;
}
@media (max-width: 767px) {
  .promo__btns {
    align-items: center;
  }
}

.promo__btns-row {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 24px;
}
@media (max-width: 767px) {
  .promo__btns-row {
    flex-direction: column;
    align-items: center;
    gap: 24px;
  }
}

.promo__descr {
  font-size: 21px;
  line-height: 36px;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
}
.promo__descr b {
  font-weight: 700;
}
@media (max-width: 1250px) {
  .promo__descr {
    width: 650px;
    max-width: 100%;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .promo__descr {
    font-size: 15px;
    line-height: 30px;
  }
}

.about__list {
  width: max-content;
  padding: 0;
  margin: 20px auto 0;
  font-size: 18px;
}
.about__list li:not(:last-child) {
  margin-bottom: 15px;
}
@media (max-width: 1250px) {
  .about__list {
    font-size: 16px;
  }
}
@media (max-width: 767px) {
  .about__list {
    width: 100%;
    margin-left: 15px;
  }
}

.descr {
  text-align: center;
  margin-top: 13px;
  font-size: 18px;
}

.card {
  position: relative;
  display: flex;
  height: 282px;
  flex-direction: column;
  align-items: center;
  background: #fef4e7;
  border-radius: 56px;
  text-align: center;
  padding: 25px 40px 25px;
}
.card_gray {
  background: rgba(217, 217, 217, 0.3);
}
.card_transparent {
  background: none;
  padding: 0;
}
.card img {
  height: 100px;
  width: 100px;
  object-fit: scale-down;
}
.card_notebook {
  background: none;
  padding: 0;
  height: auto;
}
.card_notebook img {
  width: 197px;
  height: 197px;
}
.card_notebook .h3 {
  margin-bottom: 16px;
}

.swiper-wrapper.cards-list, .cards-list {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(3, 340px);
  justify-content: space-between;
}
@media (max-width: 1250px) {
  .swiper-wrapper.cards-list, .cards-list {
    grid-template-columns: 340px;
    row-gap: 45px;
    justify-content: center;
  }
}
.swiper-wrapper.cards-list_four, .cards-list_four {
  grid-template-columns: repeat(4, 250px);
}
@media (max-width: 1250px) {
  .swiper-wrapper.cards-list_four, .cards-list_four {
    display: block;
  }
}
.swiper-wrapper.cards-list_notes, .cards-list_notes {
  grid-template-columns: repeat(4, 230px);
  row-gap: 60px;
}

@media (max-width: 1250px) {
  .swiper-wrapper.cards-list {
    display: flex;
  }
}

.price__table {
  width: 100%;
  padding: 0 20px;
  margin-top: 40px;
}
.price__table tr {
  display: grid;
  grid-template-columns: 2fr repeat(2, 1fr);
  align-items: center;
  height: 68px;
  border-bottom: 1px solid #dfe4e0;
}
.price__table th {
  text-align: left;
}
.price__table td:last-child {
  font-weight: bold;
}
@media (max-width: 767px) {
  .price__table {
    font-size: 12px;
  }
  .price__table td {
    padding-right: 10px;
  }
  .price__table td:last-child {
    padding-right: 0;
  }
}

.price-info {
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f49725;
  border-radius: 30px;
  font-weight: 700;
  font-size: 20px;
  line-height: 22px;
  color: #ffffff;
  padding: 0 11px;
  text-align: center;
}
@media (max-width: 1250px) {
  .price-info {
    width: 160px;
    margin-left: auto;
    margin-right: auto;
  }
}

.footer {
  background: #333333;
  color: #fff;
}
.footer a {
  color: inherit;
}

.footer__top {
  display: flex;
  justify-content: space-between;
  padding: 25px 0;
}
@media (max-width: 767px) {
  .footer__top {
    flex-direction: column-reverse;
  }
}

.footer__left {
  width: 490px;
  max-width: 100%;
}
@media (max-width: 1250px) {
  .footer__left {
    width: 340px;
  }
}
@media (max-width: 767px) {
  .footer__left {
    width: 100%;
  }
}

@media (max-width: 1250px) {
  .map iframe {
    height: 290px;
    width: 320px;
  }
}
@media (max-width: 767px) {
  .map iframe {
    width: 100%;
  }
}

.footer__adress {
  display: flex;
  align-items: start;
  margin-bottom: 30px;
}
.footer__adress img {
  margin-right: 10px;
}
@media (max-width: 767px) {
  .footer__adress {
    margin-top: 40px;
    width: 100%;
    font-size: 18px;
    font-weight: 600;
  }
}

.footer__logo {
  display: block;
  margin-top: 25px;
}

.footer .phone__anchor {
  margin-left: 7px;
  color: #fff;
}
@media (max-width: 1250px) {
  .footer .phone__anchor {
    font-size: 16px;
    line-height: 20px;
    font-weight: bold;
  }
}
@media (max-width: 767px) {
  .footer .phone__anchor {
    font-weight: 600;
    font-size: 20px;
  }
}

.footer__bottom {
  padding: 45px 0;
  text-align: center;
  color: #fff;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
}

div.swiper-button-prev, div.swiper-button-next {
  display: none;
}

@media (max-width: 1250px) {
  .swiper {
    width: 340px;
  }

  .swiper .cards-list {
    justify-content: start;
  }

  .swiper-wrapper .card {
    display: block;
  }

  .swiper-button-prev {
    transform: rotate(180deg);
  }

  div.swiper-button-prev,
div.swiper-button-next {
    display: block;
    width: 36px;
    height: 36px;
    background-image: url(../../img/arrow.svg);
    background-position: cover;
    background-repeat: no-repeat;
  }
  div.swiper-button-prev::after,
div.swiper-button-next::after {
    display: none;
  }
}
.header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .header__toggle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    background: #f49725;
    border: none;
    margin-right: 10px;
  }
}

.nav {
  display: none;
}
@media (max-width: 767px) {
  .nav {
    padding-top: 28px;
    padding-left: 14px;
    padding-right: 14px;
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    background-color: #fff;
    border-top: 1px solid #dfe4e0;
  }
}
.nav.hidden {
  display: none;
}

.nav__ul {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 15px;
}

.nav__anchor {
  display: flex;
  padding: 14px 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  transition: all 0.3s;
  border-bottom: 1px solid #c6c6c6;
}
.nav__anchor:hover {
  color: #f49725;
}

.nav__li:last-child .nav__anchor {
  border-bottom: none;
}